@import url(https://fonts.googleapis.com/css2?family=Teko&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  margin: 0;
  padding: 0;
  font-family: 'Teko', sans-serif;
}

.header{
    background-color: #2C2C2C;
    border-top: 5px solid gold;
    display: flex;
    width: 100%;
    height: 150px;
}

.header > img{
    display: block;
    width: 180px;
    height: 180px;
    position: absolute;
    left: 10vw;
    top: -10px;
}
.navigation{
    position: absolute;
    right: 10vw;
    width: 30vw;
    font-size: 25px;
    letter-spacing: 2px;
}

ul{
    list-style-type: none;
    padding-top: 30px;
    display: flex;
    justify-content: space-around;
}

ul > li{
    text-align: center;
}

.navigation a{
    display: block;
    color: white;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: bold;
}

ul li a:hover{
    border-bottom: 2px solid white;
}
.FrontPanel{
    background-color: #cccccc;
    height: 70vh;
    position: relative;
    overflow: hidden;
}

.FrontPanel h4{
    text-transform: uppercase;
    font-size: 40px;
    text-shadow: 0 0 5px grey;
    position: absolute;
    top: 100px;
    left: 20vw;
}

.FrontPanel h1{
    text-transform: uppercase;
    font-size: 100px;
    text-shadow: 0 0 10px grey;
    color: rgb(255, 187, 0);
    position: absolute;
    top: 100px;
    left: 15vw;
}

.FrontPanel span{
    font-size: 25px;
    font-weight: bold;
    font-style: italic;
    position: absolute;
    left: 18vw;
    top: 300px;
}

.FrontPanel button{    
    text-transform: uppercase;
    background-color: gold;
    border: none;    
    position: absolute;
    left: 20vw;
    top: 390px;
}

.FrontPanel button a{
    display: block;
    font-size: 18px;
    font-weight: bold;
    text-decoration: none;
    color: black;
    padding: 20px;
}

.FrontPanel img{
    display: block;
    width: 40vw;
    height: 75vh;
    position: absolute;
    left: 35%;
    top: -15px;
}




.AboutMePage{
    overflow: hidden;
    width: 100%;
}

.AboutMePage h1{
    text-align: left;
    font-size: 60px;
    text-transform: uppercase;
    color: #555555;
    margin: 50px 0 0 5%;
}

.AboutMePage section{
    display: flex;
    background-color: #dddddd;
    margin-bottom: 50px;
}

/*first section*/
.AboutMePage .first img{
    margin-left: 8%;
    padding-left: 200px;
    background-color: white;
}

.AboutMePage .first article{
    width: 80%;
    text-align: left;
    font-size: 25px;
}

.AboutMePage .first article h1{
    color: rgb(255, 187, 0);
    margin: 10px 0 0 20px;
}

.AboutMePage .first article span{
    width: 60%;
    display: block;
    margin: 5px 0 0 20px;
}
.AboutMePage .first article h2{
    margin: 5px 0 0 20px;
}

/*second section*/
.AboutMePage .twice img{
    margin-right: 8%;
    padding-right: 200px;
    background-color: white;
}

.AboutMePage .twice article{
    width: 80%;
    text-align: right;
    font-size: 30px;
}

.AboutMePage .twice article span{
    width: 60%;
    display: block;
    margin: 50px 0 0 38%;
}

.AboutMePage .twice article h2{
    margin: 5px 2% 0 0;
}

/*third section*/
.AboutMePage .third img{
    margin-left: 8%;
    padding-left: 100px;
    background-color: white;
    width: 25%;
}

.AboutMePage .third article{
    width: 80%;
    text-align: left;
    font-size: 30px;
}

.AboutMePage .third article span{
    width: 70%;
    display: block;
    margin: 10px 0 0 20px;
}

.AboutMePage .third article span h3{
    margin: 5px 0 15px 20px;
}
.AboutMePage .third article h3{
    margin: 40px 0 0 10px;
}

/*fourth section*/
.AboutMePage .fourth img{
    margin-right: 8%;
    padding-right: 200px;
    background-color: white;
}
.AboutMePage .fourth article{
    width: 80%;
    text-align: right;
    font-size: 30px;
}
.AboutMePage .fourth article span{
    width: 70%;
    display: block;
    margin: 50px 0 0 28%;
}
.AboutMePage .fourth article span h4{
    margin: 0;
}
.AboutMePage .fourth article span ul{
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.AboutMePage .twice article h2{
    margin: 5px 2% 0 0;
}

/*ending section*/

.AboutMePage .ending{
    background-color: white;
}

.AboutMePage .ending h1{
    text-align: center;
    width: 30%;
    border: 3px solid  rgb(255, 187, 0);
    margin: 0 auto;
    color: rgb(255, 187, 0);
}
.footer{
    color: white;
    font-size: 20px;
    font-family: 'Teko', sans-serif;
    background-color: black;
    text-align: center;
    width: 100%;
    position: relative;
    bottom: 0;
    padding: 5px 0;
}
.mediabox{
    width: 150px;
    height: 150px;
    background-color: white;
    position: fixed;
    top: 30vh;
    right: -80px;
    padding-top: 10px;
    border-radius: 20%;
}

.mediabox img{
    padding: 0;
    display: flex;
    margin: 8px 13px;
    width: 40px;
    height: 40px;
}

.mediabox img:hover{
    width: 45px;
    height: 45px;
}
.BMIcalc{
    background-color: #2C2C2C;
    width: 50%;
    display: flex;
    flex-direction: column;
    position: relative;
}

.BMIcalc h1{
    color: rgb(255, 187, 0);
    font-size: 60px;
    margin: 20px;
    text-shadow: 0 0 10px black;
}

.BMIcalc span{
    display: block;
    width: 50%;
    margin: 0 auto;
    text-align: left;
    font-size: 30px;
    display: flex;
    margin-bottom: 2%;
}

.BMIcalc span h3{
    width: 20%;
    margin: 0;
}

.BMIcalc span input{
    font-size: 20px;
    height: 30px;
    background-color: #cccccc;
    border: 2px solid rgb(255, 187, 0);
}

.BMIcalc .result{
    flex-direction: column;
}
.BMIcalc .result h3{
    width: 100%;
}

.BMIcalc button{
    font-family: 'Teko', sans-serif;
    letter-spacing: 1px;
    text-transform: uppercase;
    background-color: gold;
    border: 3px solid rgb(255, 187, 0);
    font-size: 20px;
    padding: 10px;
    width: 100px;
    position: absolute;
    right: 20%;
    top: 70%;
    font-weight: 700;
}
